
.seller {
  position: absolute;
  top: 180px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.seller .overview {
  padding: 18px;
}
.seller .overview .title {
  margin-bottom: 8px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.seller .overview .desc {
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .desc .star {
  display: inline-block;
  vertical-align: top;
}
.seller .overview .desc .text {
  margin-left: 8px;
  font-size: 10px;
  line-height: 18px;
  color: #4d555d;
}
.seller .overview .remark {
  margin-top: 18px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.seller .overview .remark .block {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  border-right: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .remark .block:last-child {
  border: none;
}
.seller .overview .remark .block h2 {
  line-height: 20px;
  font-size: 10px;
  color: #93999f;
  margin-bottom: 4px;
}
.seller .overview .remark .block .content {
  padding: 5px;
}
.seller .overview .remark .block .content .stress {
  line-height: 24px;
  font-size: 24px;
  color: #07111b;
}
.seller .overview .favorite {
  position: absolute;
  right: 18px;
  top: 18px;
  text-align: center;
  font-size: 0;
}
.seller .overview .favorite .icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url("heart_a_2.png") center center no-repeat;
  background-size: 18px;
  margin-bottom: 4px;
}
.seller .overview .favorite .icon.active {
  background: url("heart_a_1.png") center center no-repeat;
  background-size: 18px;
}
.seller .overview .favorite .text {
  line-height: 10px;
  font-size: 10px;
  color: #4d555d;
}
.seller .newsactive {
  padding: 18px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.seller .newsactive .title {
  margin-bottom: 8px;
}
.seller .newsactive .text {
  padding: 0 12px 16px;
  line-height: 2;
  font-size: 12px;
  color: #f01414;
  font-weight: 200;
}
.seller .newsactive .supports {
  border-top: 1px solid rgba(7,17,27,0.1);
}
.seller .newsactive .supports li {
  padding: 16px 12px;
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
  font-weight: 200;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.seller .newsactive .supports li .icon {
  display: inline-block;
  vertical-align: top;
  width: 16px;
  height: 16px;
  margin-right: 6px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.seller .newsactive .supports li .icon.decrease {
  background-image: url("decrease_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .newsactive .supports li .icon.decrease {
    background-image: url("decrease_3@3x.png");
}
}
.seller .newsactive .supports li .icon.discount {
  background-image: url("discount_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .newsactive .supports li .icon.discount {
    background-image: url("discount_3@3x.png");
}
}
.seller .newsactive .supports li .icon.guarantee {
  background-image: url("guarantee_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .newsactive .supports li .icon.guarantee {
    background-image: url("guarantee_3@3x.png");
}
}
.seller .newsactive .supports li .icon.invoice {
  background-image: url("invoice_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .newsactive .supports li .icon.invoice {
    background-image: url("invoice_3@3x.png");
}
}
.seller .newsactive .supports li .icon.special {
  background-image: url("special_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .newsactive .supports li .icon.special {
    background-image: url("special_3@3x.png");
}
}
.seller .pics {
  padding: 18px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.seller .pics .title {
  margin-bottom: 12px;
}
.seller .pics .pic-wrapper {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}
.seller .pics .pic-wrapper .pic-list {
  font-size: 0;
}
.seller .pics .pic-wrapper .pic-list .pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.seller .pics .pic-wrapper .pic-list .pic-item:last-child {
  margin: 0;
}


