
.ratingselect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  font-size: 0;
}
.ratingselect .rating-type .block {
  display: inline-block;
  line-height: 16px;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: #4d555c;
  font-size: 12px;
}
.ratingselect .rating-type .block.active {
  color: #fff;
}
.ratingselect .rating-type .block .count {
  font-size: 8px;
  line-height: 16px;
  margin-left: 2px;
}
.ratingselect .rating-type .block.positive {
  background: rgba(0,160,220,0.2);
}
.ratingselect .rating-type .block.positive.active {
  background: #00a0dc;
}
.ratingselect .rating-type .block.negative {
  background: rgba(77,85,93,0.2);
}
.ratingselect .rating-type .block.negative.active {
  background: #4d555d;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
}
.ratingselect .switch .icon {
  background: url("ratings_a_1.png") left top no-repeat;
  background-size: 18px;
  margin-top: 5px;
  float: left;
  width: 30px;
  height: 20px;
  font-size: 0;
}
.ratingselect .switch.active .icon {
  background: url("ratings_a_2.png") left top no-repeat;
  background-size: 18px;
}
.ratingselect .switch .text {
  font-size: 12px;
}


