.header .bulletin-wrapper {
/*height:28px*/
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(0,0,0,0.4);
  position: relative;
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  vertical-align: top;
  margin-top: 10px;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  backfround-repear: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
  margin: 0 4px;
  font-size: 10px;
}
.header .bulletin-wrapper .bulletin-more {
  position: absolute;
  right: 10px;
  botom: 10px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  transition: all 0.5s;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.header .detail.fade-enter,
.header .detail.fade-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.header .detail .detail-wrapper {
  min-height: 100%;
