
.maskFood-enter-active,
.maskFood-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.food {
  position: fixed;
  bottom: 48px;
  left: 0;
  top: 0;
  z-index: 30;
  width: 100%;
  background: #fff;
  transition: all 0.3s ease;
}
.food .food-content .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .food-content .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .food-content .back {
  position: absolute;
  top: 10px;
  left: 0;
  padding: 10px 20px;
  font-size: 30px;
  color: #fff;
}
.food .food-content .content {
  position: relative;
  padding: 18px;
  border-top: 1px solid #eee;
}
.food .food-content .content .title {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 28px;
}
.food .food-content .content .detail {
  font-size: 0;
  padding-top: 8px;
}
.food .food-content .content .detail .sell-count,
.food .food-content .content .detail .rating {
  font-size: 10px;
  color: #939c9f;
  lin-height: 10px;
}
.food .food-content .content .detail .rating {
  margin-left: 8px;
}
.food .food-content .content .price {
  padding-top: 18px;
}
.food .food-content .content .price .now,
.food .food-content .content .price .old {
  line-height: 24px;
  font-size: 14px;
  color: #f01414;
  font-weight: 700;
}
.food .food-content .content .price .old {
  font-size: 10px;
  color: #93999f;
  text-decoration: line-through;
}
.food .food-content .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  width: 74px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #fff;
  background-color: #00a0dc;
  border-radius: 12px;
}
.food .food-content .content .cartcontrol-wrapper {
  position: absolute;
  right: 18px;
  bottom: 18px;
}
.food .food-content .content .fade-enter-active,
.food .food-content .content .fade-leave-active {
  transition: all 0.3s ease;
}
.food .food-content .content .fade-enter,
.food .food-content .content .fade-leave {
  opacity: 0;
}
.food .food-content .info {
  padding: 18px;
}
.food .food-content .info .title {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 28px;
}
.food .food-content .info .text {
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
  line-height: 24px;
}
.food .food-content .rating .title {
  padding: 18px;
  padding-bottom: 0;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 28px;
}
.food .food-content .rating .rating-wrapper {
  padding: 0 18px;
}
.food .food-content .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.food .food-content .rating .rating-wrapper .rating-item .user {
  position: absolute;
  top: 16px;
  right: 0;
}
.food .food-content .rating .rating-wrapper .rating-item .user .name {
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .food-content .rating .rating-wrapper .rating-item .user img {
  border-radius: 50%;
}
.food .food-content .rating .rating-wrapper .rating-item .time {
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
  margin-bottom: 6px;
}
.food .food-content .rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-up {
  padding: 10px;
  background: url("ratings_b_2.png") left center no-repeat;
  background-size: 12px;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-down {
  padding: 10px;
  background: url("ratings_b_1.png") left center no-repeat;
  background-size: 12px;
}
.food .food-content .rating .rating-wrapper .no-ratings {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


